<template>
  <view class="uni-form-item uni-column container">
    <view class="title" v-text="title"></view>
    <input class="input uni-input" :type="type" v-model="inputValue" :placeholder="placeholder"/>
  </view>
</template>

<script>
export default {
  name: 'NormalInput',
  props: {
    title: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: ''
    },
    dataType: {
      type: String,
      default: ''
    },
    dataKey: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      inputValue: ''
    };
  },
  watch: {
    inputValue(val) {
      this.$emit('onChangeValue', {
        recordType: this.dataType,
        contentType: this.dataKey,
        value: val
      });
    }
  }
};
</script>

<style scoped>
.container {
  background-color: #FFFFFF;
  margin-bottom: 14px;
  padding: 16px;
  border-radius: 8px;
}

.title {
  color: #2E2E2E;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 10px;
}

.input {
  height: 50px;
  border-radius: 6px;
  padding: 0 14px;
  background-color: #F7F8FB;
}
</style>
